window.onload = () => {
  (async () => {
    // function copy() {
    const coupons = document.querySelectorAll(".coupon");

    coupons.forEach((item) => {
      item.addEventListener("click", () => {
        let text = item.querySelector(".code").textContent;
        navigator.clipboard.writeText(text);
      });
    });
    // }

    function open() {
      const blocks = document.querySelectorAll(".open__block");
      const stores = document.querySelector(".stores");
      const details = document.querySelector(".details");

      blocks.forEach((block) => {
        block.addEventListener("click", (e) => {
          console.log(e.target);
          if (e.target.classList.contains("open__block")) {
            stores.classList.add("dnone");
            details.classList.remove("dnone");
          }
        });
      });
    }

    open();

    function close() {
      const close = document.querySelector(".back");
      const stores = document.querySelector(".stores");
      const details = document.querySelector(".details");

      close.addEventListener("click", () => {
        details.classList.add("dnone");
        stores.classList.remove("dnone");
      });
    }
    close();
  })();
};
